import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { Layout, Form, Input, Button, Alert } from 'antd';
import hotflame from '../images/hotflame.png'
import { loginStyle, inlineStyle } from './Login.module';
import { login } from '../apis/LoginApi';
import getAES from '../apis/AESKey';
import { setEncryptionKey } from '../utils/Crypt';
import { error } from 'console';
import LoginResponse from '../interface/LoginInterface';

const { Content } = Layout;

const Login: React.FC = () => {
    let [loginMessage, setLoginMessage] = useState<string | null>(null);
    let [aesData, setAES] = useState<string | null>(null);

    let navigate = useNavigate(); // hoooks

    let onFinish = async (values: any) => {
        try {
            let aes: any = await getAES();

            setAES(aes.AESKey);

            setEncryptionKey(aesData);

            let {username, password} = values;

            let response: LoginResponse = await login(username, password);

            if (response.status === 2000) {
                //token
                const token = response.token;

                localStorage.setItem('token', token);
                navigate('/home')
                setLoginMessage('Login Successful')
            }else{
                let errorMsg = response.data[0].message;
                setLoginMessage(errorMsg);
            }
        }catch(error) {
            setLoginMessage(',!');
        }
    }

    return (
        <Layout className='login-style' style={loginStyle}>
            <Content className='login-inline-style' style={inlineStyle}>
                <div style={{ padding: '10px 0 10px 0', boxSizing: 'border-box'}}>
                    <img width='35px' src={hotflame} alt="" />
                </div>
                <Form
                    name='normal-login'
                    className='login-form'
                    initialValues={{remember: false}}
                    onFinish={onFinish}
                    style={{padding: '5px 5px', maxWidth: '100%'}}
                >
                <Form.Item
                    name='username'
                    rules={[{ required: true, message: '!' }]}
                >
                    <Input placeholder='Account | ' autoComplete='username' />
                </Form.Item>
                <Form.Item
                    name='password'
                    rules={[{ required: true, message: '!' }]}
                >
                    <Input.Password placeholder='Password | ' autoComplete='password' />
                </Form.Item>
                <Form.Item>
                    <Button type='primary' htmlType='submit' className='login-form-button' style={{width: '100%'}}>
                    
                    </Button>
                </Form.Item>
                <div style={{ textAlign: 'center', marginTop: '10px', marginBottom: '20px' }}>
                    <a href="/register" style={{ fontSize: '14px', color: '#1890ff' }}></a>
                </div>
                </Form>
            </Content>
        </Layout>
    );
}

export default Login;
